body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  min-height: 200vh;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.rub::after {
  content: " ₽";
}

.ibg {
  position: relative;
}

.ibg img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.basket__circle {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.basket__image {
  font-size: 24px;
  line-height: 0;
  color: #fff;
}

.basket_header .basket__circle {
  background-color: #009baf;
}

.basket_product-main .basket__circle {
  background-color: #dadada;
}

.basket_product-hover .basket__circle {
  background-color: #999999;
}

.basket_product-hover:hover .basket__circle {
  background-color: #f68038;
}

.product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 14px 22px;
  border: 1px solid #dadada;
  overflow: hidden;
}

.product-card__labels-block {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.product-card__label {
  height: 22px;
  padding: 0 16px;
  border-radius: 11px;
  font-size: 14px;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.product-card__label_day {
  background-color: #1ab9ce;
}

.product-card__label_new {
  background-color: #f68038;
}

.product-card__image {
  margin-bottom: 10px;
}

.product-card__image img {
  width: 100%;
}

.product-card__link {
  font-size: 15px;
  margin-bottom: 25px;
}

.product-card__link_main {
  color: #009baf;
}

.product-card__link_hover {
  color: #fff;
}

.product-card__title {
  font-weight: 900;
}

.product-card__old-price {
  z-index: 21;
  align-self: flex-end;
  font-size: 18px;
  text-decoration-line: line-through;
  color: #999999;
  cursor: default;
}

.product-card__old-price::after {
  font-size: 12px;
}

.product-card__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-card__basket_main .basket__circle {
  width: 40px;
  height: 40px;
}

.product-card__basket_hover {
  align-self: center;
}

.product-card__basket_hover .basket__image {
  font-size: 32px;
}

.product-card__basket_hover .basket__circle {
  width: 57px;
  height: 57px;
}

.product-card__button {
  display: none;
  z-index: 21;
  height: 22px;
  padding: 0 16px;
  border-radius: 11px;
  background-color: #999999;
  color: #fff;
  font-weight: 900;
  line-height: 24px;
  text-transform: uppercase;
}

.product-card__button:hover {
  background-color: #f68038;
}

.product-card__price {
  z-index: 21;
  font-size: 24px;
  font-weight: 900;
  color: #000;
  cursor: default;
}

.product-card__price::after {
  font-size: 18px;
}

.product-card__curtain {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 26px;
  border: 5px solid #f68038;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
}

.product-card__description {
  font-size: 14px;
  margin-bottom: 16px;
  cursor: default;
}

.product-card__parameter {
  color: #fff;
}

.product-card__value {
  margin-bottom: 6px;
  color: #f68038;
  font-weight: 900;
}

.product-card:hover .product-card__labels-block {
  display: none;
}

.product-card:hover .product-card__link_main {
  color: transparent;
}

.product-card:hover .product-card__basket_main {
  display: none;
}

.product-card:hover .product-card__button {
  display: block;
}

.product-card:hover .product-card__price {
  color: #fff;
}

.product-card:hover .product-card__curtain {
  opacity: 1;
}

.header {
  position: fixed;
  z-index: 50;
  width: 100%;
}

.header__up-block {
  height: 90px;
  background-color: #009baf;
  color: #fff;
}

.header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.header__menu {
  flex: 0 0 37%;
}

.header__center-element {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  display: flex;
  align-items: flex-end;
  width: 200px;
  height: 130px;
  padding-bottom: 40px;
  border-radius: 0% 0% 50% 50%/0% 0% 45% 45%;
  background-color: #009baf;
}

.header__down-block {
  height: 70px;
  background-color: #fff;
}

.menu {
  height: 100%;
  padding: 12px 0 20px;
}

.menu__icon {
  z-index: 51;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 5px 0;
  cursor: pointer;
  overflow: hidden;
}

.menu__icon.active .menu__icon-line:first-child {
  transform: translateY(9px) rotate(45deg);
}

.menu__icon.active .menu__icon-line:nth-child(2) {
  width: 0;
}

.menu__icon.active .menu__icon-line:last-child {
  transform: translateY(-9px) rotate(-45deg);
}

.menu__icon-line {
  width: 100%;
  height: 2px;
  transition: 200ms;
  background-color: #fff;
}

.navigation {
  height: 100%;
}

.navigation__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  flex-wrap: wrap;
  height: 100%;
}

.navigation__item {
  align-self: flex-start;
  margin-right: 5px;
  border-radius: 14px;
  transition: 200ms;
}

.navigation__link {
  padding: 5px 22px;
  font-family: Play, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: underline;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  width: 100%;
}

.logo__image {
  width: 180px;
}

.logo__text {
  font-size: 12px;
  font-weight: 300;
  cursor: default;
}

.phones {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4%;
  flex: 0 0 38%;
  padding: 9px 40px 18px 0;
}

.phones__icon {
  font-size: 35px;
  line-height: 25px;
}

.phones__line {
  width: 1px;
  height: 30px;
  background-color: #fff;
}

.phones-block__location {
  font-size: 12px;
  font-weight: 300;
  cursor: default;
}

.phones-block__number {
  font-size: 18px;
  font-weight: 900;
}

.link {
  display: flex;
  align-items: center;
  gap: 8px;
}

.link__icon {
  color: #999999;
  font-size: 22px;
  line-height: 0;
  transition: 200ms;
}

.link__text {
  color: #999999;
  font-size: 14px;
  font-weight: 400;
  transition: 200ms;
  border-bottom: 1px dashed #999999;
}

.link_email .link__icon {
  font-size: 20px;
}

.link_email .link__text {
  border-bottom: 1px solid #999999;
}

.input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10%;
  flex: 0 0 37%;
  padding-left: 20px;
}

.interplay {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8%;
  flex: 0 0 40%;
  padding-right: 35px;
}

.interplay__basket {
  position: relative;
}

.interplay__basket .basket__circle {
  width: 44px;
  height: 44px;
}

.interplay__amount {
  position: absolute;
  top: -8px;
  left: 28px;
  border-radius: 12px;
  padding: 4px 8px;
  background-color: #f68038;
  color: #fff;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
}

.timetable {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: default;
}

.timetable__icon {
  color: #999999;
  font-size: 28px;
  line-height: 0;
}

.timetable__text {
  color: #999999;
}

.timetable__time_bold {
  font-weight: 900;
}

.main {
  margin-top: 165px;
  min-height: 300vh;
}

.main__container {
  display: flex;
  gap: 30px;
}

.main__leftside {
  flex: 0 0 260px;
}

.main__catalogbar {
  position: relative;
  width: 100%;
  margin-bottom: 40px;
}

.main__news {
  width: 100%;
  margin-bottom: 40px;
}

.main__reviews {
  width: 100%;
}

.main__rightside {
  flex: 1 1 auto;
  min-width: 0;
}

.main__search {
  width: 100%;
  margin-bottom: 22px;
}

.main__top-slider {
  width: 100%;
  margin-bottom: 60px;
}

.main__popular {
  width: 100%;
}

.catalogbar {
  position: relative;
  width: 200%;
  color: #fff;
  overflow: hidden;
}

.catalogbar__head {
  position: relative;
  z-index: 40;
}

.ctlg-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
  height: 100px;
  padding: 15px 20px;
  background-color: #009baf;
}

.ctlg-head__title {
  flex: 0 1 65%;
  font-family: UniSansHeavyCaps, sans-serif;
  font-size: 30px;
  font-weight: 400;
  cursor: default;
}

.ctlg-head__button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 36px;
  height: 36px;
  background-color: #068292;
  cursor: pointer;
}

.ctlg-head__button.active .ctlg-head__btnline:first-child {
  transform: translateY(7px) rotate(45deg);
}

.ctlg-head__button.active .ctlg-head__btnline:nth-child(2) {
  width: 0;
}

.ctlg-head__button.active .ctlg-head__btnline:last-child {
  transform: translateY(-7px) rotate(-45deg);
}

.ctlg-head__btnline {
  width: 24px;
  height: 2px;
  background-color: #fff;
  transition: 200ms;
}

.ctlg-body {
  width: 100%;
  max-height: 0;
  transition: max-height 300ms;
  overflow: hidden;
}

.ctlg-body__list {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.ctlg-body__link {
  width: 100%;
  height: 40px;
  padding-left: 20px;
  border-bottom: 1px solid #1ab9ce;
  background-color: #009baf;
  font-size: 12px;
  font-weight: 300;
  line-height: 40px;
  cursor: pointer;
}

.ctlg-body__link_menu::before {
  position: absolute;
  right: calc(50% + 10px);
  font-size: 16px;
  line-height: 40px;
  transition: 200ms;
}

.ctlg-body__link_menu.active::before {
  transform: rotate(90deg);
}

.ctlg-body__panel {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 39;
}

.ctlg-body.active {
  max-height: 2000px;
  transition: max-height 800ms;
}

.ctlg-panel {
  width: 50%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
}

.ctlg-panel__item {
  position: relative;
  z-index: 37;
  width: 100%;
  height: 100%;
  border: 2px solid #009baf;
  border-left: none;
  background-color: #fff;
  transform: translate(-100%, 0);
  transition: 300ms;
}

.ctlg-panel__tipes {
  padding: 12px 0 12px;
  color: #1ab9ce;
}

.ctlg-panel__link {
  width: 100%;
  height: 36px;
  padding-left: 25px;
  line-height: 36px;
}

.ctlg-panel__products {
  padding: 2px;
}

.news-block__title {
  margin-bottom: 26px;
  color: #dadada;
  font-family: UniSansHeavyCaps, sans-serif;
  font-size: 30px;
  font-weight: 400;
  text-transform: uppercase;
  transition: 100ms;
}

.news-block__card {
  width: 100%;
  margin-bottom: 30px;
}

.card-news__title {
  margin-bottom: 16px;
  font-weight: 900;
  line-height: 18px;
  text-transform: uppercase;
  transition: 100ms;
}

.card-news__body {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.card-news__date {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
  color: #dadada;
}

.card-news__day {
  font-size: 24px;
  font-weight: 900;
  line-height: 24px;
}

.card-news__month {
  font-size: 18px;
  line-height: 12px;
}

.card-news__description {
  flex: 0 0 83%;
  line-height: 18px;
  color: #999999;
}

.reviews-block__title {
  margin-bottom: 26px;
  color: #dadada;
  font-family: UniSansHeavyCaps, sans-serif;
  font-size: 30px;
  font-weight: 400;
  text-transform: uppercase;
  transition: 100ms;
}

.reviews-block__card {
  width: 100%;
  margin-bottom: 20px;
}

.card-review__head {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
  line-height: 18px;
}

.card-review__author {
  font-weight: 900;
  text-transform: uppercase;
  transition: 100ms;
}

.card-review__date {
  color: #999999;
  transition: 100ms;
}

.card-review__body {
  display: flex;
  gap: 10px;
  padding: 10px;
  transition: 100ms;
}

.card-review__icon {
  color: #dadada;
  font-size: 60px;
  font-weight: 900;
  line-height: 56px;
  transition: 100ms;
}

.card-review__text {
  line-height: 18px;
  color: #999999;
  transition: 100ms;
}

.search-block {
  width: 100%;
  height: 40px;
}

.search-block__form {
  position: relative;
  z-index: 30;
  display: flex;
  width: 100%;
  height: 100%;
}

.search-block__options {
  flex: 0 0 20%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: #f3f3f3;
  color: #999999;
  cursor: pointer;
}

.search-block__magnifier::before {
  display: block;
  font-size: 20px;
  line-height: 18px;
}

.search-block__categories {
  position: relative;
  border-bottom: 1px dashed;
  font-size: 14px;
  font-weight: 400;
}

.search-block__arrows::before {
  display: block;
  font-size: 12px;
  line-height: 10px;
  transition: 300ms;
}

.search-block__arrows.active::before {
  transform: rotate(90deg);
}

.search-block__search {
  flex: 1 0 50%;
  border-top: 2px solid #f3f3f3;
  border-bottom: 2px solid #f3f3f3;
  background-color: #fff;
  padding: 0 12px;
  color: #999999;
  font-size: 14px;
}

.search-block__button {
  flex: 0 0 15%;
  background-color: #f68038;
  color: #fff;
  font-weight: 900;
  text-transform: uppercase;
}

.search-block__panel {
  position: relative;
  width: 100%;
  max-height: 0;
  z-index: 29;
  overflow: hidden;
  transition: 300ms;
}

.search-block__panel.active {
  max-height: 300px;
}

.search-block__list {
  display: grid;
  grid-template-columns: repeat(3, 30%);
  column-gap: 5%;
  row-gap: 25px;
  width: 100%;
  padding: 25px 40px;
  border-bottom: 4px solid #f68038;
  background-color: #fff;
}

.search-block__checkbox {
  display: none;
}

.search-block__checkbox:checked + .search-block__label {
  color: #f68038;
}

.search-block__label {
  color: #999999;
}

