.top-slider {
  position: relative;
  z-index: 25;
  height: 408px;
  padding-bottom: 28px;
  overflow: hidden;
}

.top-slider__wrapper {
  display: flex;
  width: 100%;
  height: 100%;
}

.top-slider__slide {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
}

.top-slider__info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.top-slider__pagination {
  display: flex;
  justify-content: center;
  gap: 45px;
  width: 100%;
  transform: translateY(-28px);
  counter-reset: item-number;
}

.top-slider__bullet {
  width: 56px;
  height: 56px;
  border: 2px solid #fff;
  border-radius: 28px;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  cursor: pointer;
}

.top-slider__bullet::after {
  counter-increment: item-number;
  content: counter(item-number);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-weight: 900;
  background-color: #f68038;
  color: #fff;
  opacity: 0;
  transition: 600ms;
}

.top-slider__bullet_active::after {
  opacity: 1;
}

